.trombone {
    cursor: pointer;
    position: relative;
    left: 20px;
    background-color: #EEE;
    margin: .5em;
    padding: .3em 0;
    height: 1.6em;
    border-radius: 4px;
    width: 15em;
    list-style-type: none;
    -webkit-box-shadow: 2px 2px;
            box-shadow: 2px 2px;
    -webkit-transition: width 1s, height 1s;
    -o-transition: width 1s, height 1s;
    transition: width 1s, height 1s;
}
.trombone .text {
    position: relative;
}
.tromboneList {
    text-decoration: none;
    height: 30px;
    position: relative;
}
#tromboneList {
    display: inline-block;
    width: 100%;
}
li.trombone {
    margin-right: 5px;
    display: inline-block;
}
.trombone:hover {
    height: 2em;
    width: 17em;
}


